/*Menu Page Colours*/
/*CSS for Colours Page*/
.coloursPage {
  text-align: center;
}
.coloursPage .titleText {
  display: inline-block;
  padding-bottom: 10px;
  border-bottom: 2px solid #fff;
  margin-bottom: 60px;
}
/*CSS for Menu Page*/
.menuPage {
  text-align: center;
}
.menuPage .insideScrollHolder {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
}
.menuPage .insideScrollHolder .insideScrollText {
  display: inline-block;
  background: #fff;
  color: #666;
  padding: 20px;
  border-radius: 2px;
  margin: 0 10px;
}
.menuPage .insideScrollHolder .insideScrollText h2 {
  color: #666;
}
.menuPage .downArrow {
  position: absolute;
  top: 93%;
  left: 50%;
  width: 25px;
}
/*CSS for Shapes Page*/
.shapesPage {
  /*Adjust the Modal Container to have no gutter*/
}
.shapesPage .textHolder {
  width: 35%;
  float: right;
  text-align: left;
  padding: 10px 40px;
  padding-left: 20px;
}
.shapesPage .textHolder .titleText {
  display: inline-block;
  padding-bottom: 10px;
  border-bottom: 2px solid #fff;
  margin-bottom: 20px;
}
.shapesPage .textHolder .titleText h2 {
  margin-top: 0px;
}
.shapesPage .textHolder .subText {
  margin-top: 30px;
}
.shapesPage .textHolder .subText h4 {
  color: red;
}
.shapesPage .imageHolder {
  margin-top: 30px;
  width: 65%;
}
.shapesPage button {
  width: 100%;
}
.shapesPage .container {
  width: auto;
}
.shapesPage .container .show-grid {
  margin-right: 0;
  margin-left: 0;
}
.shapesPage .container .show-grid > .col,
.shapesPage .container .show-grid > [class*="col-"] {
  padding-right: 3px;
  padding-left: 5px;
}
/* On Desktops */
@media screen and (min-width: 768px) {
  /* Hide row padding for the 2nd Image*/
  .row-padding {
    display: none;
  }
}
/* On screens that are 768px or less, set the Module Wrapper width 100% */
@media screen and (max-width: 768px) {
  .shapesPage .textHolder {
    width: 100%;
    float: none;
    padding: 10px;
  }
  .shapesPage .imageHolder {
    margin-top: 10px;
    width: 100%;
  }
}
/*CSS for Global Elements*/
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #000;
}
h1,
h2,
h3,
h4,
p {
  color: #fff;
}
p {
  font-size: 18px;
}
/*Page Transition Effects*/
.fade-enter {
  opacity: 0.01;
}
.fade-enter.fade-enter-active {
  opacity: 1;
  transition: opacity 600ms ease-in;
}
.fade-exit {
  opacity: 1;
}
.fade-exit.fade-exit-active {
  opacity: 0;
}
/*Globla Button Style*/
button {
  padding: 0px;
  border: 0px;
}
button img {
  width: 100%;
  height: auto;
}
img {
  width: 100%;
}
.modal-header .close {
  position: absolute;
  right: 4px;
  top: 0px;
}
/* On Desktops */
@media screen and (min-width: 768px) {
  .modal-dialog {
    width: 410px;
    margin: 30px auto;
  }
}
